import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export default class DetailsPrice extends Component {
    constructor() {
        super();
        this.state = {
            open: false,
            quantity: 1,
            stock: 'InStock'
        };
    }

    minusQty = () => {
        if (this.state.quantity > 1) {
            this.setState({
                quantity: this.state.quantity - 1,
                stock: 'InStock'
            });
        }
    }

    plusQty = () => {
        if (this.props.item.stock >= this.state.quantity) {
            this.setState({
                quantity: this.state.quantity + 1,
                stock: 'InStock'
            });
        } else {
            this.setState({
                stock: 'Out of Stock !'
            });
        }
    }

    changeQty = (e) => {
        this.setState({
            quantity: parseInt(e.target.value)
        });
    }

    render() {
        const { symbol, item, addToCartClicked, BuynowClicked, addToWishlistClicked } = this.props;
        return (
            <div className="col-lg-6 rtl-text">
                <div className="product-right">
                    <h2> {item.name} </h2>
                    <h4>
                        <del>{symbol} {item.price} </del>
                        <span>{symbol}{item.discount} off</span></h4>
                    <h3> {symbol} {item.price - item.discount} </h3>

                    <div className="product-description border-product">

                        <span className="instock-cls"> {this.state.stock} </span>
                        <h6 className="product-title">quantity</h6>
                        <div className="qty-box">
                            <div className="input-group">
                                <span className="input-group-prepend">
                                    <button type="button" className="btn quantity-left-minus" data-type="minus"
                                        onClick={this.minusQty} data-field="">
                                        <i className="fa fa-angle-left"></i>
                                    </button>
                                </span>
                                <input type="text" name="quantity" className="form-control input-number"
                                    value={this.state.quantity} onChange={this.changeQty}
                                />
                                <span className="input-group-prepend">
                                    <button type="button" className="btn quantity-right-plus" data-type="plus"
                                        onClick={this.plusQty} data-field="">
                                        <i className="fa fa-angle-right"></i>
                                    </button>
                                </span>
                            </div>
                        </div>
                    </div>
                    <div className="product-buttons" >
                        <a className="btn btn-solid" onClick={() => addToCartClicked(item, this.state.quantity)} >add to cart</a>
                        <Link to={`${process.env.PUBLIC_URL}/checkout`} className="btn btn-slide"
                            onClick={() => BuynowClicked(item, this.state.quantity)} > buy now</Link>

                    </div>
                    <div className="border-product">
                        <h6 className="product-title">product details</h6>
                        <p> {item.shortDetails} </p>
                    </div>
                    <div className="border-product">
                        <div className="product-icon">
                            <button className="wishlist-btn" onClick={() => addToWishlistClicked(item)} ><i
                                className="fa fa-heart"></i><span
                                    className="title-font">Add To WishList</span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
