import React, { Component } from 'react'
import { connect } from 'react-redux'
import InputRange from 'react-input-range'
import 'react-input-range/lib/css/index.css'
import SlideToggle from 'react-slide-toggle'

import { getBrands, getColors, getMinMaxPrice } from '../../../services'
import { addToWishlistData, filterBrand, filterColor, filterPrice } from '../../../actions'
import {Link} from 'react-router-dom'

class Filter extends Component {
    constructor() {
        super();
        this.state = {
            openFilter: false
        };
        this.activeItem = '';
    }

    closeFilter = () => {
        document.querySelector(".collection-filter").style = "left:-365px";
    }
    clickBrandHandle(event, brands) {
        var index = brands.indexOf(event.target.value);

        if (event.target.checked) {
            brands.push(event.target.value);//add
        } else {
            brands.splice(index, 1); // remove
        }

        this.props.filterBrand(brands);
    }

    colorHandle(event, color) {
        var elems = document.querySelectorAll(".color-selector ul li");

        for (var i = 0; i < elems.length; i++) {
            elems[i].classList.remove("active");
        }

        if(this.activeItem === color){
            this.props.filterColor('');
            this.activeItem = '';
        } else {
            event.target.classList.add("active");
            this.props.filterColor(color);
            this.activeItem = color;
        }
    }

    render() {
        const filteredBrands = this.props.filters.brand;

        return (
            <div className="collection-filter-block">
                {/*brand filter start*/}
                <div className="collection-mobile-back">
                    <span className="filter-back" onClick={this.closeFilter}>
                        <i className="fa fa-angle-left" aria-hidden="true"></i> back
                        </span>
                </div>
                <SlideToggle>
                   {({ onToggle, setCollapsibleElement }) => (
                        <div className="collection-collapse-block">
                            <h3 className="collapse-block-title" onClick={onToggle} >category</h3>
                            <div className="collection-collapse-block-content" ref={setCollapsibleElement}>
                                <div className="collection-brand-filter">
                                    <ul className="category-list">
                                        <li><Link  to={`${process.env.PUBLIC_URL}/left-sidebar/collection/men`} >Men</Link> </li>
                                        <li><Link  to={`${process.env.PUBLIC_URL}/left-sidebar/collection/women`} >Women</Link> </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    )}
                </SlideToggle>
                <SlideToggle>
                    {({ onToggle, setCollapsibleElement }) => (
                        <div className="collection-collapse-block">
                            <h3 className="collapse-block-title" onClick={onToggle}>brand</h3>
                            <div className="collection-collapse-block-content" ref={setCollapsibleElement}>
                                <div className="collection-brand-filter">
                                    {this.props.brands.map((brand, index) => {
                                        return (
                                            <div className="custom-control custom-checkbox collectionfilter-checkbox" key={index} >
                                                <input type="checkbox"
                                                    onClick={(e) => this.clickBrandHandle(e, filteredBrands)}
                                                    value={brand}
                                                    defaultChecked={filteredBrands.includes(brand) ? true : false}
                                                    className="custom-control-input"
                                                    id={brand}
                                                />
                                                <label className="custom-control-label" htmlFor={brand}
                                                >{brand}</label>
                                            </div>
                                        )
                                    })}
                                </div>
                            </div>
                        </div>
                    )}
                </SlideToggle>
                {/*color filter start here*/}
                <SlideToggle>
                    {({ onToggle, setCollapsibleElement }) => (
                        <div className="collection-collapse-block">
                            <h3 className="collapse-block-title" onClick={onToggle}>colors</h3>
                            <div className="collection-collapse-block-content" ref={setCollapsibleElement}>
                                <div className="color-selector">
                                    <ul>
                                        {this.props.colors.map((color, index) => {
                                            return (
                                                <li key={index} className={color}
                                                    title={color}
                                                    onClick={(e) => this.colorHandle(e, color)}
                                                >
                                                </li>
                                            )
                                        })}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    )}
                </SlideToggle>
                {/*price filter start here */}
                <SlideToggle>
                    {({ onToggle, setCollapsibleElement }) => (
                        <div className="collection-collapse-block open">
                            <h3 className="collapse-block-title" onClick={onToggle}>price</h3>
                            <div className="collection-collapse-block-content block-price-content" ref={setCollapsibleElement}>
                                <div className="collection-brand-filter">
                                    <div className="custom-control custom-checkbox collection-filtercheckbox">
                                        <br/>
                                        <InputRange
                                            maxValue={this.props.prices.max}
                                            minValue={this.props.prices.min}
                                            value={this.props.filters.value}
                                            onChange={value => this.props.filterPrice({ value })} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    )}
                </SlideToggle>
            </div>
        )
    }
}


const mapStateToProps = state => ({
    brands: getBrands(state.data.products), mapStateToProps,
    colors: getColors(state.data.products), mapStateToProps,
    prices: getMinMaxPrice(state.data.products), mapStateToProps,
    filters: state.filters
})

export default connect(mapStateToProps, { filterBrand, filterColor, filterPrice })(Filter);