import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import './index.scss';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Fashion from './components/layouts/fashion/main';

import Layout from './components/app'
import { Provider } from 'react-redux';
import store from './store';
import { getAllProducts } from './actions';

import Cart from './components/cart';
import Wishlist from './components/wishlist';
import Compare from './components/compare';

import About from './components/pages/about-us';
import Contact from './components/pages/contact';
import Faq from './components/pages/faq';
import PageNotFound from './components/pages/404';

import Login from './components/pages/login';
import Register from './components/pages/register';

import LeftSideBar from './components/products/left-sidebar';

import { IntlReducer as Intl, IntlProvider } from 'react-redux-multilingual';
import translations from './constants/translations';

import CollectionLeftSidebar from './components/collection/collection-left-sidebar';

import { history } from './services/history';

import Checkout from './components/checkout/index'
import orderSuccess from './components/checkout/success'

class Root extends Component {
  render() {
    store.dispatch(getAllProducts());

    return (
      <Provider store={store} >
      <IntlProvider translations={translations} locale='en'>
        <BrowserRouter>
          <Switch>
            <Layout>
            <Route exact path={`${process.env.PUBLIC_URL}/`} component={Fashion} />
              <Route exact path={`${process.env.PUBLIC_URL}/fashion`} component={Fashion} />
              <Route exact path={`${process.env.PUBLIC_URL}/cart`} component={Cart} />
              <Route exact path={`${process.env.PUBLIC_URL}/wishlist`} component={Wishlist} />
              <Route exact path={`${process.env.PUBLIC_URL}/compare`} component={Compare} />

              <Route exact path={`${process.env.PUBLIC_URL}/pages/about-us`} component={About} />
              <Route exact path={`${process.env.PUBLIC_URL}/pages/404`} component={PageNotFound} />
              <Route exact path={`${process.env.PUBLIC_URL}/pages/contact-us`} component={Contact} />
              <Route exact path={`${process.env.PUBLIC_URL}/pages/faq`} component={Faq} />

              <Route exact path={`${process.env.PUBLIC_URL}/pages/login`} component={Login} />
              <Route exact path={`${process.env.PUBLIC_URL}/pages/register`} component={Register} />

              <Route exact path={`${process.env.PUBLIC_URL}/left-sidebar/product/:id`} component={LeftSideBar} />

              <Route exact path={`${process.env.PUBLIC_URL}/left-sidebar/collection/:category`} component={CollectionLeftSidebar} />
              
              <Route exact path={`${process.env.PUBLIC_URL}/checkout`} component={Checkout} />
              <Route exact path={`${process.env.PUBLIC_URL}/order-success`} component={orderSuccess} />
              
            </Layout>
          </Switch>
        </BrowserRouter>
        </IntlProvider>
      </Provider>
    )
  }
}

ReactDOM.render(<Root />, document.getElementById("root"));