import Global from '../helpers/global';

export const dataService = {
    getCategoryWiseData,
    getAllBrands,
    getAllProductList
};


function getCategoryWiseData() {
    const requestOptions = {
        method: 'GET'
    };

    return fetch(Global.BASE_API_PATH + `Category/GetAll/`, requestOptions)
        .then(handleResponse)
        .then(res => {
            return res;
        });
}

function getAllBrands() {
    const requestOptions = {
        method: 'GET'
    };

    return fetch(Global.BASE_API_PATH + `BrandLogo/GetAll/`, requestOptions)
        .then(handleResponse)
        .then(res => {
            return res;
        });
}
function getAllProductList() {
    const requestOptions = {
        method: 'GET'
    };

    return fetch(Global.BASE_API_PATH + `ProductMaster/GetProductList/`, requestOptions)
        .then(handleResponse)
        .then(res => {
            return res;
        });
}


function handleResponse(response) {
    return response.text().then(text => {
        const data = JSON.parse(text);

        if (!response.ok) {
            if (response.status === 401) {

            }

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}