import React, { Component } from 'react'
import { Helmet } from 'react-helmet'
import Breadcrumb from '../common/breadcrumb'
import NewProduct from '../common/new-product'
import Filter from './common/filter';
import FilterBar from './common/filter-bar'
import ProductListing from './common/product-listing'
import StickyBox from 'react-sticky-box'

import { withRouter } from 'react-router-dom'

class CollectionLeftSidebar extends Component {
    constructor() {
        super();
        this.state = {
            layoutColumns: 4
        };
    }

    LayoutViewClicked(columns) {
        this.setState({
            layoutColumns: columns
        });
    }

    openFilter = () => {
        document.querySelector(".collection-filter").style = "left: -15px";
    }

    render() {
        return (
            <div>
                {/*SEO Support*/}
                <Helmet>
                    <title>Fairoma | Collection of Products</title>
                    <meta name="description" content="Fairoma Mall" />
                </Helmet>
                {/*SEO Support End */}
                <Breadcrumb title={'Collection'} />
                <section className="section-b-space">
                    <div className="collection-wrapper">
                        <div className="container">
                            <div className="row">
                                <div className="col-sm-3 collection-filter">
                                    <StickyBox>
                                        <div>
                                            <Filter />
                                            <NewProduct />
                                        </div>
                                    </StickyBox>
                                    {/*side-bar banner end here*/}
                                </div>
                                <div className="collection-content col">
                                    <div className="page-main-content ">
                                        <div className="">
                                            <div className="row">
                                                <div className="col-sm-12">
                                                    <div className="top-banner-wrapper">
                                                        <a href={null}>
                                                            {this.props.match.params.category === 'men'
                                                                ? <img className="img-fluid" alt="mem image" src={`${process.env.PUBLIC_URL}/assets/images/mega-menu/2.jpg`} />
                                                                : <img className="img-fluid" alt="women image" src={`${process.env.PUBLIC_URL}/assets/images/mega-menu/1.jpg`} />
                                                            }
                                                        </a>
                                                        <div className="top-banner-content small-section">
                                                            <h4>fashion</h4>
                                                            <h5>Fairoma</h5>
                                                            <p>Fairoma Mall</p>
                                                        </div>
                                                    </div>
                                                    <div className="collection-product-wrapper">
                                                        <div className="product-top-filter">
                                                            <div className="container-fluid p-0">
                                                                <div className="row">
                                                                    <div className="col-xl-12">
                                                                        <div className="filter-main-btn">
                                                                            <span onClick={this.openFilter}
                                                                                className="filter-btn btn btn-theme"><i
                                                                                    className="fa fa-filter"
                                                                                    aria-hidden="true"></i> Filter</span>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <div className="row">
                                                                    <div className="col-12">
                                                                        <FilterBar onLayoutViewClicked={(columns) => this.LayoutViewClicked(columns)}
                                                                            category={this.props.match.params.category} />
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        {/*Products Listing Component*/}
                                                        <ProductListing colSize={this.state.layoutColumns} category={this.props.match.params.category} />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}

export default withRouter(CollectionLeftSidebar)
