import React, { Component } from 'react'
import Slider from 'react-slick'
import { Slider6 } from '../../../services/scripts';
import { dataService } from '../../../services/data.service';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

export default class LogoBlock extends Component {
    constructor() {
        super();
        this.state = { data: [] };
    }

    componentDidMount() {
        this.getAllBrands();
    }

    getAllBrands() {
        dataService.getAllBrands()
            .then(
                res => {
                    if (res.isSuccess) {
                        this.setState({ data: res.data });
                    } else {
                        toast.error(res.errors[0], "Fashion Store");
                    }
                },
                error => {
                    toast.error("Something went wrong !!", "Fashion Store");
                }
            )
    }
    render() {
        const { data } = this.state;
        return (
            <section className="section-b-space">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <Slider {...Slider6} className="product-6 no arrow" >
                                {
                                    data.map((logo, index) => {
                                        return (<div className="logo-block" key={index} >
                                            <a href={null} >
                                                <img src={logo.imagePath} />
                                            </a>
                                        </div>
                                        )
                                    })
                                }
                            </Slider>
                        </div>
                    </div>
                </div>
                <ToastContainer />
            </section>
        )
    }
}
