import React, { Component } from 'react'
import '../../common/index.scss';
import { Helmet } from 'react-helmet';

import HomeSlider from '../common/home-slider';
import TopCollection from '../common/top-collection';
import CollectionBanner from '../common/collection-banner';
import ParallaxBanner from '../common/parallax-banner';
import SpecialProducts from '../common/special-products';
import LogoBlock from '../common/logo-block';

export default class Fashion extends Component {
    render() {
        return (
            <div>
                <Helmet>
                    <title>Fairoma Mall | Fashion Store</title>
                    <meta name="description" content="Fairoma Mall | Fashion Store" />
                </Helmet>
                <HomeSlider />
                <CollectionBanner />
                <TopCollection type="" />
                <ParallaxBanner />
                <SpecialProducts />
                <LogoBlock />
            </div>
        )
    }
}
