import React from 'react'
import { Link } from 'react-router-dom'

const CartHeader = ({ item, total, symbol, removeFromCart }) => (
    <li>
        <div className="media">
            <Link to={`${process.env.PUBLIC_URL}/left-sidebar/product/${item.id}`}  >
                <img alt="image" src={`${item.pictures[0]}`} />
            </Link>
            <div className="media-body" >
                <Link to={`${process.env.PUBLIC_URL}/left-sidebar/product/${item.id}`}  >
                    <h4>{item.name}</h4>
                </Link>
                <h4><span>{item.qty} X {symbol} {(item.price - item.discount)}</span></h4>
            </div>
        </div>
        <div className="close-circle">
            <a href={null} onClick={removeFromCart} ><i className="fa fa-times-circle" ></i></a>
        </div>
    </li>
)

export default CartHeader;