import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import InfiniteScroll from 'react-infinite-scroll-component'
import { getVisibleproducts } from '../../../services'
import { addToCart, addToWishlist, addToCompare } from '../../../actions'
import ProductListItem from './product-list-item'

class ProductListing extends Component {
    constructor() {
        super();
        this.state = {
            limit: 5,
            hasMoreItems: true
        };
    }

    fetchMoreItems = () => {
        if (this.state.limit >= this.props.products.length) {
            this.setState({ hasMoreItems: false });
            return;
        }

        setTimeout(() => {
            this.setState({ limit: this.state.limit + 5 })
        }, 3000);
    }
    render() {
        const { symbol, products, addToCart, addToWishlist, addToCompare } = this.props;

        return (
            <div>
                <div className="product-wrapper-grid">
                    <div className="container-fluid">
                        {products.length > 0 ?
                            <InfiniteScroll
                                dataLength={this.state.limit}
                                next={this.fetchMoreItems}
                                hasMore={this.state.hasMoreItems}
                                loader={<div className="loading-cls"></div>}
                                endMessage={
                                    <p className="seen-cls seen-it-cls" >
                                        <b>Yay! You have seen it all</b>
                                    </p>
                                }
                            >
                                <div className="row">
                                    {products.slice(0, this.state.limit).map((product, index) =>
                                        <div
                                            className={`${this.props.colSize === 3 ? 'col-xl-3 col-md-6 col-grid-box' : 'col-lg-' + this.props.colSize}`}
                                            key={index} >
                                            <ProductListItem
                                                product={product}
                                                symbol={symbol}
                                                onAddToCartClicked={() => addToCart(product)}
                                                onAddToWishlistClicked={() => addToWishlist(product)}
                                                onAddToCompareClicked={() => addToCompare(product)}
                                            />
                                        </div>
                                    )}
                                </div>
                            </InfiniteScroll>

                            :
                            <div className="row">
                                <div className="col-sm-12 text-center section-b-space mt-5 no-found" >
                                    <img className="img-fluid mb-4" src={`${process.env.PUBLIC_URL}/assets/images/empty-search.jpg`} />
                                    <h3>Sorry! Couldn't find the product you were looking For!!! </h3>
                                    <p>Please check if you have misspelt something or try searching with other words.</p>
                                    <Link to={`${process.env.PUBLIC_URL}/`} className="btn btn-solid" >continue shopping</Link>
                                </div>
                            </div>
                        }
                    </div>
                </div>
            </div>

        )
    }
}

const mapStateToProps = (state,props) => ({
    products: getVisibleproducts(state.data, state.filters,props.category),
    symbol: state.data.symbol
})

export default connect(mapStateToProps, { addToCart, addToWishlist, addToCompare })(ProductListing);