import { ADD_TO_CART, REMOVE_FROM_CART, DECREMENT_QTY } from '../constants/ActionTypes';

export default function cartReducer(state = { cart: [] }, action) {
    switch (action.type) {
        case ADD_TO_CART:
           // debugger;
            const productId = action.product.id;
            if (state.cart.findIndex(product => product.id === productId) !== -1) {
                const cart = state.cart.reduce((cartAcc, product) => {
                    if (product.id === productId) {
                        cartAcc.push({ ...product, qty: product.qty + 1, sum: (product.price - product.discount) * (product.qty+1) });
                    } else {
                        cartAcc.push(product);
                    }
                    return cartAcc;
                }, [])

                return { ...state, cart }
            }

            return { ...state, cart: [...state.cart, { ...action.product, qty: action.qty, sum: (action.product.price - action.product.discount) * action.qty }] }

        case DECREMENT_QTY:
            debugger;
            if (state.cart.findIndex(product => product.id === action.productId) !== -1) {
                const cart = state.cart.reduce((cartAcc, product) => {
                    if (product.id === action.productId && product.qty > 1) {
                        cartAcc.push({ ...product, qty: product.qty - 1, sum: (product.price - product.discount) * (product.qty-1) });
                    } else {
                        cartAcc.push(product);
                    }
                    return cartAcc;
                }, [])

                return { ...state, cart }
            }
            return { ...state, cart: [...state.cart, { ...action.product, qty: action.qty, sum: (action.product.price - action.product.discount) * action.qty }] }

        case REMOVE_FROM_CART:
            debugger;
            return {
                cart: state.cart.filter(item => item.id !== action.product_Id.id)
            }

        default:
            { }
    }

    return state;
}
