import Global from '../helpers/global';

export const userService = {
    login,
    register,
    doPayment
};

async function login(username, password) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ username, password })
    };

    return fetch(Global.BASE_API_PATH + `UserMaster/Login/`, requestOptions)
        .then(handleResponse)
        .then(res => {
            return res;
        });
}

async function register(obj) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
    };

    return fetch(Global.BASE_API_PATH + `UserMaster/Save/`, requestOptions)
        .then(handleResponse)
        .then(res => {
            return res;
        });
}
async function doPayment(obj) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
    };

    return fetch(Global.BASE_API_PATH + `PaymentMaster/Save/`, requestOptions)
        .then(handleResponse)
        .then(res => {
            return res;
        });
}

function handleResponse(response) {
    return response.text().then(text => {
        const data = JSON.parse(text);

        if (!response.ok) {
            if (response.status === 401) {

            }

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}