import React, { Component } from 'react'
import { addToCart, addtoCartData, addToWishlist } from '../../actions';
import { connect } from 'react-redux';
import { Helmet } from 'react-helmet'
import Slider from 'react-slick'

import Breadcrumb from '../common/breadcrumb';
import CategoryBlock from '../common/category-block'
import NewProduct from '../common/new-product'
import DetailsTopTabs from './common/details-top-tabs'
import DetailsWithPrice from './common/product/details-price'
import SmallImages from './common/product/small-image'
import ImageZoom from './common/product/image-zoom'
import { ToastContainer } from 'react-toastify'

class LeftSideBar extends Component {
    constructor() {
        super();

        this.state = {
            open: false,
            nav1: null,
            nav2: null
        };
    }

    componentDidMount() {
        this.setState({
            nav1: this.slider1,
            nav2: this.slider2
        });
    }

    backClick() {
        document.getElementById("filter").style.left = "-365px";
    }

    render() {
        const { symbol, item, addToCart, addtoCartData, addToWishlist } = this.props;

        var products = {
            slidesToShow: 1,
            slidesToScroll: 1,
            dots: false,
            arrows: true,
            fade: true
        };
        var productsnav = {
            slidesToShow: 3,
            swipeToSlide: true,
            arrows: false,
            dots: false,
            focusOnSelect: true
        };


        return (
            <div>
                {/*SEO Support*/}
                <Helmet>
                    <title>Fairoma  | {item.category} | {item.name} </title>
                    <meta name="description" content="Fairoma mall  Fashion" />
                </Helmet>
                {/*SEO Support End */}
                <Breadcrumb parent={'Product'} title={item.name} />
                {/*Section Start*/}
                {(item) ?
                    <section className="section-b-space">
                        <div className="collection-wrapper">
                            <div className="container">
                                <div className="row">
                                    <div className="col-sm-3 collection-filter" id="filter">
                                        <div className="collection-mobile-back pl-5">
                                            <span onClick={this.backClick} className="filter-back">
                                                <i className="fa fa-angle-left" aria-hidden="true"></i> back
                                        </span>
                                        </div>
                                        <CategoryBlock />
                                        <NewProduct />
                                    </div>
                                    <div className="col-lg-9 col-sm-12 col-xs-12">
                                        <div className="">
                                            <div className="row">
                                                <div className="col-lg-6 product-thumbnail">
                                                    <Slider {...products} asNavFor={this.state.nav2}
                                                        ref={slider => { this.slider1 = slider }}
                                                        className="product-slick"
                                                    >
                                                        {item.variants.length ?
                                                            item.variants.map((vari, i) =>
                                                                <div key={i} >
                                                                    <ImageZoom image={vari.images} />
                                                                </div>
                                                            ) :
                                                            item.pictures.map((vari, i) =>
                                                                <div key={i} >
                                                                    <ImageZoom image={vari.images} />
                                                                </div>
                                                            )}

                                                    </Slider>
                                                    <SmallImages item={item} settings={productsnav} navOne={this.state.nav1} />
                                                </div>
                                                <DetailsWithPrice
                                                    symbol={symbol}
                                                    item={item}
                                                    addToCartClicked={addToCart}
                                                    BuynowClicked={addtoCartData}
                                                    addToWishlistClicked={addToWishlist}
                                                    navOne={this.state.nav1}
                                                />
                                            </div>
                                        </div>
                                        <DetailsTopTabs item={item} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                    : ''}
                {/*Section End*/}
                <ToastContainer />
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    let productId = ownProps.match.params.id;
    return {
        item: state.data.products.find(el => el.id === parseInt(productId)),
        symbol: state.data.symbol
    }
}

export default connect(mapStateToProps, { addToCart, addtoCartData, addToWishlist })(LeftSideBar);