import React, { Component } from 'react'
import { Link } from "react-router-dom";
import { withTranslate } from 'react-redux-multilingual';

class Navbar extends Component {
    constructor() {
        super();

        this.state = {
            navClose: { right: '0px' }
        };
    }

    openNav = () => {
        this.setState({ navClose: { right: '0px' } });
    }

    closeNav = () => {
        this.setState({ navClose: { right: '-410px' } });
    }

    render() {
        const {translate} = this.props;
        return (
            <div>
                <div>
                    <div className="main-navbar">
                        <div id="mainnav">
                            <div className="toggle-nav" onClick={this.openNav}>
                                <i className="fa fa-bars sidebar-bar"></i>
                            </div>
                            <ul className="nav-menu">
                                <li className="back-btn" onClick={this.closeNav} >
                                    <div className="mobile-back text-right">
                                        <span >Back</span>
                                        <i className="fa fa-angle-right pl-2" aria-hidden="true"></i>
                                    </div>
                                </li>
                                <li>
                                    <Link to="/" className="nav-link" > {translate('home')}  </Link>
                                </li>
                                <li >
                                    <Link to="#" className="nav-link" >{translate('shop')}</Link>
                                    <span className="sub-arrow"></span>
                                    <ul className="nav-submenu">
                                        <li>
                                            <Link to={`${process.env.PUBLIC_URL}/left-sidebar/collection/men`} > {translate('mens_fashion')} </Link>
                                        </li>
                                        <li>
                                            <Link to={`${process.env.PUBLIC_URL}/left-sidebar/collection/women`} > {translate('women_fashion')}  </Link>
                                        </li>
                                      
                                    </ul>
                                </li>

                                <li>
                                    <Link to="#" className="nav-link" > {translate('pages')} 
                                    <span className="sub-arrow"></span>
                                    </Link>
                                    <ul className="nav-submenu">
                                        <li><Link to={`${process.env.PUBLIC_URL}/pages/about-us`} > {translate('about_us')}  </Link></li>
                                        <li><Link to={`${process.env.PUBLIC_URL}/pages/faq`} > {translate('FAQ')} </Link></li>
                                        <li><Link to={`${process.env.PUBLIC_URL}/pages/contact-us`} > {translate('contact')}  </Link></li>
                                        <li><Link to={`${process.env.PUBLIC_URL}/pages/register`} > {translate('register')} </Link></li>
                                        <li><Link to={`${process.env.PUBLIC_URL}/pages/login`} > {translate('login')} </Link></li>
                                    </ul>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withTranslate(Navbar);